import React, { Component, lazy, Suspense } from "react";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { GlobalStyle } from "../theme/GlobalStyle.styled";
import Loader from "../components/Loader";

//        Suspense,
//       (,  )
//     .
import Searchbar from "../components/Searchbar";
// const Searchbar = lazy(() => import("../components/Searchbar"));
const ImageGallery = lazy(() => import("../components/ImageGallery"));

export default class App extends Component {
  // .   state.  .      
  state = {
    searchQuery: "",
    page: null,
    selectedOption: null,
  };

  //   state,     App  SearchBar     input     
  handleSearchBarSubmit = (searchQuery, page) => {
    this.setState({
      searchQuery,
      page: 1,
    });
    // console.log(this.state.foundImgs);
  };
  //   state,     App  SearchBar     Select    PerPage
  //    App  Callback handleSubmitPerPage.      "selectedOption"   Searchbar
  //        ,    setState.
  handleSubmitPerPage = (selectedOption) => {
    this.setState(selectedOption);
  };
  render() {
    return (
      <>
        <ToastContainer autoClose={3000} />
        <GlobalStyle />
        <Suspense
          fallback={
            <div>
              Loading...
              <Loader />
            </div>
          }
        >
          <Searchbar
            onSubmit={this.handleSearchBarSubmit}
            //    Searchbar   ( ) onChangeApp  handleSubmitPerPage
            onChangeApp={this.handleSubmitPerPage}
          />
          <ImageGallery
            searchQuery={this.state.searchQuery}
            page={this.state.page}
            selectedOption={this.state.selectedOption}
          />
        </Suspense>
      </>
    );
  }
}
