import React, { Component } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";
import { IconContext } from "react-icons";
import { ImSearch } from "react-icons/im";
import {
  SearchbarHeader,
  SearchForm,
  SearchFormButton,
  SearchFormButtonLabel,
  SearchFormInput,
} from "./Searchbar.styled";
import SelectMarks from "../SelectMarks/";

export default class Searchbar extends Component {
  state = {
    foundImgs: "",
    selectedOption: null,
  };

  //    state     input
  handleNameChange = (event) => {
    this.setState({ foundImgs: event.currentTarget.value.toLowerCase() });
  };

  //   props  state  App
  handleSubmit = (event) => {
    event.preventDefault();
    if (this.state.foundImgs.trim() === "") {
      toast.error(" ");
      return;
    }
    //          state    
    if (!this.state.selectedOption) {
      toast.error(
        "  per page,      . "
      );
      return;
    }
    this.props.onSubmit(this.state.foundImgs);
    this.setState({ foundImgs: "" });
  };
  //   props {selectedOption}  state  App    state Searchbar
  //    ( Searchbar)    SelectMarks,      
  //   onChangeSelect,         "onChange"  SelectMarks.
  //        selectedOption,   
  //     App.
  onChangeSelect = (selectedOption) => {
    this.setState({ selectedOption });
    this.props.onChangeApp(selectedOption); //    this.props.onChangeApp(),     App    props Searchbar
  };

  render() {
    return (
      <SearchbarHeader>
        <SearchForm onSubmit={this.handleSubmit}>
          <SearchFormInput
            type="text"
            name="foundImgs"
            // value      state  handleSubmit
            value={this.state.foundImgs}
            autocomplete="off"
            autoFocus={true}
            placeholder="Search images and photos"
            onChange={this.handleNameChange}
          />
          <SearchFormButton type="submit">
            <SearchFormButtonLabel>Search</SearchFormButtonLabel>
            <IconContext.Provider value={{ color: "blue", size: 30 }}>
              <ImSearch />
            </IconContext.Provider>
          </SearchFormButton>
        </SearchForm>
        {/* // Callback  onChangeSelect    props onChange */}
        <SelectMarks onChange={this.onChangeSelect} />
      </SearchbarHeader>
    );
  }
}

Searchbar.propTypes = {
  onSubmit: PropTypes.func,
};
